import _ from 'lodash'
import { v4 as uuidv4 } from 'uuid';
import { isValidPhoneNumber, parsePhoneNumberFromString } from 'libphonenumber-js'
import {createPhoneNumber}  from "./phoneGenerator";


const random = (min = 0, max = Number.MAX_SAFE_INTEGER - 1) => {
	return min + Math.floor(Math.random() * Math.floor(max + 1 - min));
}
const randomFirstName = () => {
	return _firstname[random(0, _firstname.length - 1)];
}
const randomLastName = () => {
	return _lastname[random(0, _lastname.length - 1)];
}
const randomUsername = (prefix) => {
    const id = uuidv4();
    const shorter = id.slice(0, 12);
    return `${prefix}_${shorter}`
}
const randomJobTile = () => {
	return _jobTitle[random(0, _jobTitle.length - 1)];
}
const randomCompany = () => {
	return _company[random(0, _company.length - 1)];
}
const randomDescriptionCompany = () => {
	return _desCompany[random(0, _desCompany.length - 1)];
}
const randomEmail = (template = 'gmail.com', firstname, lastname) => {
    return `${_.toLower(firstname)}_${_.toLower(lastname)}@${template}`
}

const randomPhoneNumber = (countryCode) => {
    const phoneNumber = createPhoneNumber(countryCode)
    const isValid = isValidPhoneNumber(phoneNumber, countryCode)
    if (!isValid) {
        randomPhoneNumber()
    }

    return phoneNumber
}

const randomOptions = (options) => {
	return options[random(0, options.length - 1)];
}

export const generatorRecords = (
    template,
    numberOfRow,
    { settings: { usernamePrefix, phoneCountryCode, emailTemplate, ouOptions, externalCompanyIdOptions, languageOptions, tagOptions, parentCompanyNameOptions } }
  ) => {
      const firstLine = template.map((item) => item.value);
      const result = [firstLine.join(',')];
      const total = +numberOfRow
      for (let line = 1; line <= total; line++) {
        const csvFileLine = [];

        template.map((item) => {
          const username = randomUsername(usernamePrefix);
          const firstname = randomFirstName();
          const lastname = randomLastName();
          const email = randomEmail(emailTemplate, firstname, lastname);

          switch (item.type) {
            case 'username':
              csvFileLine.push(username);
              break;
            case 'firstname':
              csvFileLine.push(firstname);
              break;
            case 'lastname':
              csvFileLine.push(lastname);
              break;
            case 'email':
              csvFileLine.push(email);
              break;
            case 'phone':
              csvFileLine.push(randomPhoneNumber(phoneCountryCode));
              break;
            case 'ou':
              csvFileLine.push(randomOptions(ouOptions));
              break;
            case 'job':
              csvFileLine.push(randomJobTile());
              break;
            case 'externalCompanyId':
              csvFileLine.push(randomOptions(externalCompanyIdOptions));
              break;
            case 'language':
              csvFileLine.push(randomOptions(languageOptions));
              break;
            case 'tag':
              csvFileLine.push(randomOptions(tagOptions));
              break;
            case 'externalCompanyName':
              csvFileLine.push(randomCompany());
              break;
            case 'parentCompanyName':
              csvFileLine.push(randomOptions(parentCompanyNameOptions));
              break;
            case 'description':
              csvFileLine.push(randomDescriptionCompany());
              break;
            default:
              break;
          }
        });
  
        const record = csvFileLine.join(',');
        result.push(record);
      }

    downloadFile(result, usernamePrefix)
  };
  

const downloadFile = (result, name) => {
    const fileName = `import_${name}`
    const content = result.join('\n')

    var blob = new Blob([content], { type: 'text/csv' });
    if (navigator.msSaveBlob) { // IE 10+
        navigator.msSaveBlob(blob, fileName);
    } else {
        var link = document.createElement("a");
        if (link.download !== undefined) {
            var url = URL.createObjectURL(blob);
            link.setAttribute("href", url);
            link.setAttribute("download", fileName);
            link.style.visibility = 'hidden';
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
        }
    }

    return 
}

